<template>
  <div class="container">
    <h1>Pokemon's</h1>
    <PokemonSearch
      :apiUrl="apiUrl"
      @setPokemonUrl="setPokemonUrl"
    ></PokemonSearch>
    <pokemon-list
      :imageUrl="imageUrl"
      :apiUrl="apiUrl"
      @setPokemonUrl="setPokemonUrl"
    ></pokemon-list>
    <PokemonDetail
      v-if="showDetail"
      :pokemonUrl="pokemonUrl"
      :imageUrl="imageUrl"
      @closeDetail="closeDetail"
    ></PokemonDetail>
  </div>
</template>

<script>
import PokemonSearch from "./PokemonSearch";
import PokemonList from "./PokemonList";
import PokemonDetail from "./PokemonDetail";

export default {
  name: "Pokemon",
  data() {
    return {
      imageUrl:
        "https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/",
      apiUrl: "https://pokeapi.co/api/v2/pokemon/",
      pokemonUrl: "",
      showDetail: false,
    };
  },
  components: {
    PokemonSearch,
    PokemonList,
    PokemonDetail,
  },
  methods: {
    setPokemonUrl(url) {
      this.pokemonUrl = url;
      this.showDetail = true;
    },
    closeDetail() {
      this.pokemonUrl = "";
      this.showDetail = false;
    },
  },
};
</script>

